






























































import { Component, Prop, Vue, Emit } from 'vue-property-decorator';
import { Firebase } from '../service/Firebase';

@Component({})
export default class SideComp extends Vue {
  @Prop() windowSize: any;
  @Prop() currentUser: any;
  @Emit('signInGoogle') signInGoogle(user:any){}
  @Emit('signOut') signOut(user:any){}

  drawer:boolean = false;
  miniVariant:boolean = false;
  items:any = [
    { icon: 'date_range', iconClass: 'amber lighten-1 white--text', title: '', subtitle: '  ', link:"meetings"},
    { icon: 'person_add', iconClass: 'amber lighten-1 white--text', title: '', subtitle: '  ', link:"register"},
    { icon: 'people', iconClass: 'amber lighten-1 white--text', title: '', subtitle: '  ', link:"management"},
    { icon: 'insert_chart', iconClass: 'amber lighten-1 white--text', title: '', subtitle: '  ', link:"statistics"}
  ];
  toggleDrawer(){
    this.drawer = !this.drawer;
  }
  goPage(link:string){
    this.$router.push(link);
  }
}
